.constructor-start-page {
  height: 584px;
  border-radius: 4px;
  box-shadow: 0 2px 0 0 #dededf;
  display: flex;
  display: none;
}

.constructor-aside {
  width: 400px;
  background: #fff;
  border-radius: 4px 0 0 4px;
  box-shadow: 12px 0 12px -12px rgba(9, 18, 29, 0.5);
  position: relative;
  z-index: 2;
}

.constructor-tabbed {
  height: 100%;
}

.constructor-list {
  display: flex;
  padding: 16px 24px 0;
  border-bottom: 1px solid #cdd2d7;
}

.constructor-list__item {
  margin-right: 24px;
}
.constructor-list__item a {
  color: #0054b9;
  line-height: 1.25;
  font-weight: 600;
  font-size: 16px;
  padding-bottom: 12px;
  transition: 0.3s ease;
  position: relative;
}
.constructor-list__item a:hover {
  color: #f33e3e;
}
.constructor-list__item a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  background: #f33e3e;
  display: block;
  bottom: 0;
  left: 0;
  transition: 0.3s ease;
  opacity: 0;
}
.constructor-list__item:last-item {
  margin-right: 0;
}

.section-tabs-controls {
  padding: 37px 16px 37px 24px;
  height: calc(100% - 49px);
  overflow-y: auto;
}
.section-tabs-controls:focus {
  outline: none;
}

.section-tabs-title {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
  color: #2b2a28;
  margin-bottom: 18px;
}

.control-constructor {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 28px;
}

.control-constructor__item-button {
  display: inline-flex;
  flex-wrap: wrap;
  width: calc(33.3333333333% - 16px);
  margin-right: 16px;
  margin-bottom: 27px;
}
.control-constructor__item-button .constructor-img {
  height: 100%;
  width: 100%;
  transition: 0.3s ease;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
  display: block;
}
.control-constructor__item-button:hover .img-container {
  box-shadow: 0 0 0 2px rgba(168, 176, 186, 0.3);
}
.control-constructor__item-button.active .img-container {
  border-color: #0054b9;
  box-shadow: 0 0 0 2px rgba(0, 84, 185, 0.3);
}
.control-constructor__item-button:focus {
  outline: none;
}
.control-constructor__item-button:focus .img-container {
  border-color: #0054b9;
}

.constructor-text {
  font-size: 14px;
  line-height: 1.43;
  color: #2b2a28;
  margin-top: 9px;
}

.img-container {
  background: #fff;
  height: 80px;
  border: 1px solid #a8b0ba;
  transition: 0.3s ease;
  border-radius: 2px;
  overflow: hidden;
}

.constructor-list__item a[aria-selected=true] {
  color: #2b2a28;
}

.constructor-list__item a[aria-selected=true]::before {
  opacity: 1;
}

.control-constructor__item-radio {
  font-size: 16px;
  line-height: 1.5;
  padding-left: 28px;
  position: relative;
  margin-right: 24px;
}
.control-constructor__item-radio::before {
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 1px solid #a8b0ba;
  display: block;
  position: absolute;
  left: 0;
  top: 4px;
  transition: 0.3s ease;
  z-index: 2;
}
.control-constructor__item-radio::after {
  content: "";
  position: absolute;
  width: calc(100% + 24px);
  height: calc(100% + 8px);
  border-radius: 16px;
  background: #ebecef;
  left: -8px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  transition: 0.3s ease;
  opacity: 0;
}
.control-constructor__item-radio span {
  position: relative;
  z-index: 2;
}
.control-constructor__item-radio:hover::after {
  opacity: 1;
}
.control-constructor__item-radio.active::before {
  border: 4px solid #0054b9;
}
.control-constructor__item-radio:focus {
  outline: none;
}
.control-constructor__item-radio:focus::before {
  border-color: #0054b9;
}

.control-constructor__item-radio:last-child {
  margin-right: 0;
}

.control-constructor__item-color {
  width: 32px;
  height: 32px;
  border-radius: 2px;
  margin-right: 16px;
  position: relative;
  transition: 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-constructor__item-color:hover {
  box-shadow: 0 0 0 3px rgba(0, 84, 185, 0.3);
}
.control-constructor__item-color.active::before {
  content: "";
  width: 12px;
  height: 12px;
  background: url("../images/check_2.svg") no-repeat center;
  background-size: 100%;
}

.control-constructor__item-color:last-child {
  margin-right: 0;
}

